<template>
  <div class="works_content_page" id="works_content_page">
    <div class="intro_part" id="intro_part">
      <div class="intro_object" id="intro_object">
        <div class="object_inline">Type: {{works_content.tag_list[0]}}</div>
        <div class="object_inline">Client: {{works_content.client}}</div>
        <div class="object_inline">Year: {{works_content.year}}</div>
      </div>
      <div class="intro_title" id="intro_title">{{works_content.title}}</div>
      <div class="intro_content" id="intro_content">{{works_content.intro}}</div>
    </div>
    
    <div class="works_part" id="works_part">

    </div>

    <Menu></Menu>
  </div>
</template>

<style scoped lang="css">
div {
  font-family: MyriadVariableConcept-Roman;
  color: #37383d;
}

::-webkit-scrollbar {
  display: none;
}

div.works_content_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

div.intro_part {
  display: grid;
  font-size: 40rem;
  /* grid-template-columns: 400rem 100rem; */
}

div.intro_object {
  grid-row: 1;
  grid-column: 2;
  align-self: center;
}

div.intro_title {
  grid-row: 2;
  grid-column: 1;
  align-self: center;
  /* width: 30rem; */
}

div.intro_content {
  grid-row: 2;
  grid-column: 2;
  align-self: center;
}
</style>

<script>
import global from "../../global";
import anime from "../../../../modules/anime";
import Menu from "../../../components/menu/Menu";

//export default
export default {
  name: "Works_Content",

  data() {
    return {
      global: global,
      id: this.$route.params.id,
      works_content: require("../../../../static/works/"+this.$route.params.id + "/config.js")["works_content_"+global.lang],
      works_imgs:require("../../../../static/works/"+this.$route.params.id + "/config.js").img_list,
    };
  },
  components: {
    Menu,
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {},
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    this.refresh_root_size();
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
  },
  destroyed() {},
  watch: {
    // watch the size change to refresh
    "global.width": function (val, oldVal) {
      this.refresh_root_size();
    },
  },
  methods: {
    refresh_root_size() {
      let static_piexl = 700;
      if (global.width < static_piexl) {
        document.documentElement.style.fontSize = static_piexl / 1920 + "px";
        return;
      }
      document.documentElement.style.fontSize = global.width / 1920 + "px";
    },
  },
};
</script>



